export class User {
    static currentUser = {
        id: '1',
        full_name: 'Test User',
        email: 'test@example.com',
        user_type: 'customer',
        role: 'user'
    };

    static async me() {
        return this.currentUser;
    }

    static async login() {
        // Mock login - in real app this would redirect to auth
        console.log('Login called');
        return this.currentUser;
    }

    static async logout() {
        console.log('Logout called');
    }

    static async updateMyUserData(data) {
        this.currentUser = { ...this.currentUser, ...data };
        return this.currentUser;
    }
}