import { supabase } from '../supabaseClient';

/**
 * Encapsula a lógica para interagir com os pedidos no Supabase.
 */
export class Order {

    /**
     * Cria um novo pedido e seus itens.
     * * @param {string} customerId O ID do perfil do cliente que está fazendo o pedido.
     * @param {Array} cartItems Um array de itens do carrinho. Ex: [{ product_id, quantity, price_at_purchase }]
     * @param {object} shippingDetails Um objeto com os detalhes de entrega. Ex: { shipping_address_line1, shipping_city, ... }
     * @param {number} totalAmount O valor total calculado do pedido.
     * @returns {Promise<object>} Os dados do pedido recém-criado.
     */
    static async createOrder(customerId, cartItems, shippingDetails, totalAmount) {
        // --- MELHOR PRÁTICA (a ser implementada no futuro) ---
        // A criação de um pedido envolve múltiplas etapas (inserir na tabela 'orders' e depois na 'order_items').
        // O ideal é que isso seja uma "transação" (ou tudo funciona, ou nada funciona).
        // A melhor maneira de garantir isso no Supabase é usando uma "Edge Function" (RPC).
        // Por enquanto, faremos no lado do cliente, mas tenha em mente que não é 100% seguro contra falhas parciais.

        // 1. Inserir o pedido principal na tabela 'orders'
        const { data: orderData, error: orderError } = await supabase
            .from('orders')
            .insert({
                customer_id: customerId,
                total_amount: totalAmount,
                ...shippingDetails // Espalha os detalhes de entrega nas colunas correspondentes
            })
            .select() // .select() faz com que a linha recém-inserida seja retornada
            .single(); // Esperamos apenas um resultado de volta

        if (orderError) {
            console.error('Erro ao criar o pedido:', orderError);
            throw orderError;
        }

        // 2. Preparar os itens do pedido, associando-os ao ID do pedido recém-criado
        const itemsToInsert = cartItems.map(item => ({
            order_id: orderData.id,
            product_id: item.product_id,
            quantity: item.quantity,
            price_at_purchase: item.price_at_purchase,
        }));

        // 3. Inserir todos os itens na tabela 'order_items'
        const { error: itemsError } = await supabase
            .from('order_items')
            .insert(itemsToInsert);

        if (itemsError) {
            console.error('Erro ao inserir itens do pedido:', itemsError);
            // ATENÇÃO: Neste ponto, o pedido foi criado, mas seus itens não.
            // Este é o risco de não usar uma transação/Edge Function.
            // Para um app real, uma Edge Function que faz tudo isso no servidor é o ideal.
            throw itemsError;
        }

        return orderData;
    }

    /**
     * Busca todos os pedidos de um cliente específico.
     * * @param {string} customerId O ID do perfil do cliente.
     * @returns {Promise<Array>} Uma lista dos pedidos do cliente, incluindo os itens e produtos.
     */
    static async getOrdersByCustomer(customerId) {
        // Esta query é poderosa: ela busca os pedidos e, para cada pedido,
        // busca os 'order_items' e, para cada item, os detalhes do 'products'.
        const { data: orders, error } = await supabase
            .from('orders')
            .select(`
                *,
                order_items (
                    *,
                    products (*)
                )
            `)
            .eq('customer_id', customerId)
            .order('created_at', { ascending: false }); // Ordena os mais recentes primeiro

        if (error) {
            console.error('Erro ao buscar pedidos do cliente:', error);
            throw error;
        }

        return orders;
    }

    /**
     * Busca os detalhes de um único pedido pelo seu ID.
     * * @param {string} orderId O ID do pedido.
     * @returns {Promise<object>} Os detalhes completos do pedido.
     */
    static async getOrderById(orderId) {
    const { data: order, error } = await supabase
        .from('orders')
        .select(`
            *,
            order_items (
                *,
                products (*)
            )
        `)
        .eq('id', orderId)
        .single();

    if (error) {
        console.error('Erro ao buscar pedido por ID:', error);
        throw error;
    }

    return order;
}
}