import { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext'; // 1. Usando o AuthContext
import { User } from '../entities/User';
import { Product } from '../entities/Product';
import { Order } from '../entities/Order';
import {
    Users, Package, ShoppingBag, DollarSign, Activity, Search, UserCog, CheckCheck, X, AlertCircle, Check
} from 'lucide-react';
import { Button } from '../components/ui/button';
import { Input } from '../components/ui/input';
import { Card, CardHeader, CardTitle, CardContent, CardDescription } from '../components/ui/card';
import { Skeleton } from '../components/ui/skeleton';
import { Badge } from '../components/ui/badge';
import { Tabs, TabsList, TabsTrigger } from "../components/ui/tabs";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogFooter } from "../components/ui/dialog";
import { Alert, AlertDescription } from '../components/ui/alert';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "../components/ui/table";


export default function AdminDashboard() {
    // Seus estados locais estão bons para gerenciar a UI da página
    const [loading, setLoading] = useState(true);
    const [users, setUsers] = useState([]);
    const [products, setProducts] = useState([]);
    const [orders, setOrders] = useState([]);
    const [activeTab, setActiveTab] = useState("overview");
    const [searchQuery, setSearchQuery] = useState('');
    const [selectedUser, setSelectedUser] = useState(null);
    const [isUserDialogOpen, setIsUserDialogOpen] = useState(false);
    const [successMessage, setSuccessMessage] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const navigate = useNavigate();

    // 2. Obtendo o usuário e o estado de carregamento do AuthContext
    const { userProfile, loading: authLoading } = useAuth();

    // 3. Funções de busca de dados refatoradas e envolvidas em useCallback
    const loadUsers = useCallback(async () => {
        try {
            // Supondo que você refatorou User.js para ter um método estático 'list' ou 'getAll'
            const allUsers = await User.list();
            setUsers(allUsers || []);
        } catch (error) {
            console.error("Error loading users:", error);
            setErrorMessage("Falha ao carregar usuários.");
        }
    }, []);

    const loadProducts = useCallback(async () => {
        try {
            const allProducts = await Product.list();
            setProducts(allProducts || []);
        } catch (error) {
            console.error("Error loading products:", error);
            setErrorMessage("Falha ao carregar produtos.");
        }
    }, []);

    const loadOrders = useCallback(async () => {
        try {
            // Supondo que Order.js tenha um método 'listAll' para admin
            // A função que criamos era getOrdersByCustomer, então vamos precisar de uma nova
            const allOrders = await Order.listAll(); // Você precisará criar este método em Order.js
            setOrders(allOrders || []);
        } catch (error) {
            console.error("Error loading orders:", error);
            setErrorMessage("Falha ao carregar pedidos.");
        }
    }, []);

    // 4. useEffect principal refatorado
    useEffect(() => {
        // Roda apenas quando a verificação de autenticação terminar
        if (!authLoading) {
            if (userProfile?.user_type === 'admin') {
                // Se o usuário é admin, carrega todos os dados
                const loadAllData = async () => {
                    setLoading(true);
                    await Promise.all([
                        loadUsers(),
                        loadProducts(),
                        loadOrders()
                    ]);
                    setLoading(false);
                };
                loadAllData();
            } else {
                // Se não for admin, redireciona para a home
                navigate('/');
            }
        }
    }, [userProfile, authLoading, navigate, loadUsers, loadProducts, loadOrders]);

    const verifyUser = async (userId) => {
        try {
            // Supondo que você refatorou User.js para ter um método 'update'
            await User.update(userId, { is_verified: true });
            setIsUserDialogOpen(false);
            showSuccess('Usuário verificado com sucesso');
            await loadUsers(); // Recarrega a lista de usuários
        } catch (error) {
            console.error("Error verifying user:", error);
            setErrorMessage('Erro ao verificar usuário. Tente novamente.');
        }
    };

    // Suas funções de ajuda e UI estão ótimas!
    const openUserDialog = (user) => { setSelectedUser(user); setIsUserDialogOpen(true); };
    const showSuccess = (message) => { setSuccessMessage(message); setTimeout(() => { setSuccessMessage(''); }, 3000); };
    const getVerificationStatus = (user) => { /* ... seu código ... */ };
    const getUserTypeLabel = (type) => { /* ... seu código ... */ };
    const filteredUsers = users.filter(user => (user.full_name?.toLowerCase().includes(searchQuery.toLowerCase()) || user.email?.toLowerCase().includes(searchQuery.toLowerCase())));
    const getOrdersCount = () => orders.length;
    const getProductsCount = () => products.length;
    // 5. AJUSTE: Usando a propriedade 'total_amount' da tabela 'orders'
    const getRevenueTotal = () => orders.reduce((total, order) => total + (order.total_amount || 0), 0);
    const getSuppliersCount = () => users.filter(user => user.user_type === 'supplier').length;
    const getCustomersCount = () => users.filter(user => user.user_type === 'customer').length;
    const getPendingVerifications = () => users.filter(user => user.user_type === 'supplier' && !user.is_verified).length;

    return (
        <div className="min-h-screen bg-gray-50 py-8">
            <div className="container mx-auto px-4">
                {/* O JSX permanece praticamente o mesmo, mas com alguns ajustes de nome de propriedade.
                  A lógica de renderização condicional, os cards e tabelas estão excelentes.
                */}

                {/* Exemplo de ajuste na tabela de pedidos recentes */}
                {/* ... */}
                {/* <p className="text-sm text-gray-500">R$ {order.total_price?.toFixed(2)}</p> */} {/* MUDAR PARA: */}
                {/* <p className="text-sm text-gray-500">R$ {order.total_amount?.toFixed(2)}</p> */}
                {/* ... */}

                {/* Exemplo de ajuste na tabela de produtos */}
                {/* ... */}
                {/* <TableCell>{product.stock} {product.unit}</TableCell> */} {/* MUDAR PARA: */}
                {/* <TableCell>{product.stock_quantity}</TableCell> */}
                {/* <TableCell>{new Date(product.created_date).toLocaleDateString()}</TableCell> */} {/* MUDAR PARA: */}
                {/* <TableCell>{new Date(product.created_at).toLocaleDateString()}</TableCell> */}
                {/* ... */}
            </div>
        </div>
    );
}