import { supabase } from '../supabaseClient';

/**
 * Encapsula a lógica para interagir com os produtos no Supabase.
 */
export class Product {

    /**
     * Lista todos os produtos do banco de dados.
     * @returns {Promise<Array>} Uma lista de todos os produtos.
     */
    static async list() {
        const { data, error } = await supabase
            .from('products')
            .select('*')
            .order('created_at', { ascending: false }); // Ordena os mais recentes primeiro

        if (error) {
            console.error('Erro ao listar produtos:', error);
            throw error;
        }

        return data;
    }

    /**
     * Busca um único produto pelo seu ID.
     * @param {string} id - O UUID do produto.
     * @returns {Promise<object>} Os dados do produto encontrado.
     */
    static async getById(id) {
        const { data, error } = await supabase
            .from('products')
            .select('*')
            .eq('id', id)
            .single(); // .single() retorna um objeto em vez de um array

        if (error) {
            console.error('Erro ao buscar produto por ID:', error);
            throw error;
        }

        return data;
    }

    /**
     * Cria um novo produto no banco de dados.
     * (Requer permissões de RLS para INSERT na tabela 'products')
     * @param {object} productData - Um objeto com os dados do novo produto.
     * Ex: { name, description, price, category, stock_quantity, supplier_id }
     * @returns {Promise<object>} Os dados do produto recém-criado.
     */
    static async create(productData) {
        const { data, error } = await supabase
            .from('products')
            .insert([productData])
            .select()
            .single();

        if (error) {
            console.error('Erro ao criar produto:', error);
            throw error;
        }

        return data;
    }

    /**
     * Atualiza um produto existente.
     * (Requer permissões de RLS para UPDATE na tabela 'products')
     * @param {string} id - O UUID do produto a ser atualizado.
     * @param {object} updateData - Um objeto com os campos a serem atualizados.
     * @returns {Promise<object>} Os dados do produto atualizado.
     */
    static async update(id, updateData) {
        const { data, error } = await supabase
            .from('products')
            .update(updateData)
            .eq('id', id)
            .select()
            .single();

        if (error) {
            console.error('Erro ao atualizar produto:', error);
            throw error;
        }

        return data;
    }

    /**
     * Deleta um produto.
     * (Requer permissões de RLS para DELETE na tabela 'products')
     * @param {string} id - O UUID do produto a ser deletado.
     * @returns {Promise<void>}
     */
    static async delete(id) {
        const { error } = await supabase
            .from('products')
            .delete()
            .eq('id', id);

        if (error) {
            console.error('Erro ao deletar produto:', error);
            throw error;
        }
    }
}