// src/contexts/CartContext.js
import { createContext, useState, useContext, useMemo } from 'react';

const CartContext = createContext();

export function CartProvider({ children }) {
    const [cartItems, setCartItems] = useState([]);

    const addToCart = (productToAdd, quantity = 1) => {
        setCartItems(prevItems => {
            const existingItem = prevItems.find(item => item.product.id === productToAdd.id);

            if (existingItem) {
                // Se o item já existe, apenas aumenta a quantidade
                return prevItems.map(item =>
                    item.product.id === productToAdd.id
                        ? { ...item, quantity: item.quantity + quantity }
                        : item
                );
            } else {
                // Se é um item novo, adiciona ao carrinho
                return [...prevItems, { product: productToAdd, quantity }];
            }
        });
        console.log(`${productToAdd.name} adicionado ao carrinho!`);
    };

    const removeFromCart = (productId) => {
        setCartItems(prevItems => prevItems.filter(item => item.product.id !== productId));
    };

    const updateQuantity = (productId, newQuantity) => {
        if (newQuantity < 1) {
            removeFromCart(productId);
            return;
        }
        setCartItems(prevItems =>
            prevItems.map(item =>
                item.product.id === productId
                    ? { ...item, quantity: newQuantity }
                    : item
            )
        );
    };

    const clearCart = () => {
        setCartItems([]);
    };

    // Usamos useMemo para otimizar, assim como no AuthContext
    const value = useMemo(() => ({
        cartItems,
        addToCart,
        removeFromCart,
        updateQuantity,
        clearCart
    }), [cartItems]);

    return <CartContext.Provider value={value}>{children}</CartContext.Provider>;
}

// Hook customizado para facilitar o uso do contexto
export function useCart() {
    return useContext(CartContext);
}