import { createContext, useState, useEffect, useContext, useMemo } from 'react';
import { supabase } from '../supabaseClient';

const AuthContext = createContext();

export function AuthProvider({ children }) {
    const [session, setSession] = useState(null);
    const [userProfile, setUserProfile] = useState(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        // --- EFEITO 1: Apenas para a sessão e o loading inicial ---

        // Pega a sessão inicial.
        supabase.auth.getSession().then(({ data: { session } }) => {
            setSession(session);
            // O loading inicial da autenticação termina aqui.
            setLoading(false);
        });

        // Ouve por futuras mudanças na autenticação (login/logout).
        const { data: { subscription } } = supabase.auth.onAuthStateChange(
            (_event, session) => {
                setSession(session);
            }
        );

        return () => subscription.unsubscribe();
    }, []);


    // --- EFEITO 2: Apenas para buscar o perfil QUANDO a sessão mudar ---
    useEffect(() => {
        // Se não há sessão, limpa o perfil.
        if (!session?.user) {
            setUserProfile(null);
            return;
        }

        // Se há uma sessão, busca os dados do perfil.
        supabase
            .from('profiles')
            .select('*')
            .eq('id', session.user.id)
            .single()
            .then(({ data, error }) => {
                if (error && error.code !== 'PGRST116') {
                    console.error("Erro ao buscar perfil:", error);
                } else {
                    setUserProfile(data);
                }
            });

    }, [session]); // Roda sempre que a 'session' mudar.


    const value = useMemo(() => ({
        session,
        userProfile,
        loading
    }), [session, userProfile, loading]);

    return (
        <AuthContext.Provider value={value}>
            {children}
        </AuthContext.Provider>
    );
}

export function useAuth() {
    return useContext(AuthContext);
}