// src/services/productService.js
import { supabase } from '../supabaseClient';

export const listProducts = async () => {
    const { data, error } = await supabase
        .from('products')
        .select('*')
        .order('created_at', { ascending: false });

    if (error) {
        console.error('Erro ao listar produtos:', error);
        throw error;
    }
    return data;
};

export const getProductById = async (id) => {
    const { data, error } = await supabase
        .from('products')
        .select('*')
        .eq('id', id)
        .single();

    if (error) {
        console.error('Erro ao buscar produto por ID:', error);
        throw error;
    }
    return data;
};

/**
 * Lista todos os produtos de um fornecedor específico.
 * @param {string} supplierId - O ID do fornecedor.
 * @returns {Promise<Array>} Uma lista de produtos do fornecedor.
 */
export const listBySupplier = async (supplierId) => {
    const { data, error } = await supabase
        .from('products')
        .select('*')
        .eq('supplier_id', supplierId) // A mágica está aqui: filtra pelo ID do fornecedor
        .order('created_at', { ascending: false });

    if (error) {
        console.error('Erro ao listar produtos por fornecedor:', error);
        throw error;
    }

    return data;
};

// --- FUNÇÕES ADICIONADAS ---

/**
 * Cria um novo produto no banco de dados.
 * @param {object} productData - Os dados do produto a serem criados.
 */
export const create = async (productData) => {
    const { data, error } = await supabase
        .from('products')
        .insert([productData])
        .select()
        .single();

    if (error) {
        console.error('Erro ao criar produto:', error);
        throw error;
    }
    return data;
};

/**
 * Atualiza um produto existente.
 * @param {string} id - O ID do produto a ser atualizado.
 * @param {object} updateData - Os novos dados do produto.
 */
export const update = async (id, updateData) => {
    const { data, error } = await supabase
        .from('products')
        .update(updateData)
        .eq('id', id)
        .select()
        .single();

    if (error) {
        console.error('Erro ao atualizar produto:', error);
        throw error;
    }
    return data;
};

/**
 * Deleta um produto pelo seu ID.
 * @param {string} id - O ID do produto a ser deletado.
 */
export const deleteById = async (id) => {
    const { error } = await supabase
        .from('products')
        .delete()
        .eq('id', id);

    if (error) {
        console.error('Erro ao deletar produto:', error);
        throw error;
    }
};
